  //       }
  //     });
  //   }).catch(error => {
  //     AntDModal.error({
  //       title: '',
  //       content: error.response.data.msg,
  //       onOk() {
  //         _this.getTableDatas(1, 10);
  //       }
  //     });
  //   });
  // }

  // // 
  // @action doVerifyResult = (verifyId) => {
  //   const _this = this;
  //   verifyResult(verifyId).then((response) => {
  //     console.info(response);
  //     AntDModal.success({
