// 
import React, { Component } from 'react';
import { action } from 'mobx';
import { inject, observer } from 'mobx-react';
import classNames from 'classnames';
import {
  Form as AntDForm,
  Input as AntDInput,
  Button as AntDButton,
  Row as AntDRow,
  Col as AntDCol,
  Select as AntDSelect

} from 'antd';

// 
import styles from './style.module.less';

// 
import { searchTableData } from '../../../../../api/modelManagement/modelRelease';

const AntDFormItem = AntDForm.Item;
const AntDOption = AntDSelect.Option;

@AntDForm.create()
@observer
class SearchForm extends Component {
  @action handleSearch = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      for (let key in values) {
        if (!values[key]) {
          values[key] = "";
        }
      }
      searchTableData(values).then((response) => {
        console.info(response);
        response.data.dataSource.map((item) => {
          switch(item.state) {
            case 0: item.state = '';break;
            case 1: item.state = '';break;
            case 2: item.state = '';break;
            case 3: item.state = '';break;
            case 4: item.state = '';break;
            case 5: item.state = '';break;
            case 6: item.state = '';break;
            case 7: item.state = '';break;
            case 8: item.state = '';break;
            case -1: item.state = '';break;
            default: item.state = '';
          }
          item.releaseState === 0 ?  item.releaseState = '' :  item.releaseState = '';
          return item.key = item.verifyId;
        });
        this.props.ds.setDataSource(response.data.dataSource);
        this.props.ds.setTotal(response.data.total);
      }).catch(error => {
        console.info(error);
      });
      console.info('');
    });
  }

  @action handleReset = () => {
    this.props.form.resetFields();
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <div className={classNames(styles.componentDemo, this.props.className)} style={this.props.style}>
        <AntDForm className={classNames(styles.searchForm)} onSubmit={this.handleSearch}>
          <AntDRow gutter={24}>
            <AntDCol span={8}>
              <AntDFormItem label=''>
                {getFieldDecorator('name')(
                  <AntDInput placeholder='' />
                )}
              </AntDFormItem>
            </AntDCol>
            <AntDCol span={8}>
              <AntDFormItem label=''>
                {getFieldDecorator('releaseState')(
                  <AntDSelect allowClear={true}>
                    <AntDOption value='0'></AntDOption>
                    <AntDOption value='1'></AntDOption>
                  </AntDSelect>
                )}
              </AntDFormItem>
            </AntDCol>
            <AntDCol span={8}>
              <AntDFormItem label=''>
                {getFieldDecorator('version')(
                  <AntDInput placeholder='' />
                )}
              </AntDFormItem>
            </AntDCol>
          </AntDRow>
          <AntDRow>
            <AntDCol span={24} style={{ textAlign: 'right' }}>
              <AntDButton type="primary" htmlType="submit"></AntDButton>
              <AntDButton style={{ marginLeft: 8 }} onClick={this.handleReset}></AntDButton>
            </AntDCol>
          </AntDRow>
        </AntDForm>
      </div>
    );
  }
}

SearchForm.defaultProps = {
};

// const InjectSearchForm = inject(({someStore={}}) => ({someProps: someStore.attribute}))(SearchForm);
const InjectSearchForm = inject()(SearchForm);

export {
  SearchForm,
  InjectSearchForm
};


