import {
  request,
  request2
} from '../../utils/request.axios';

const base = '/hkai-admin/api/rest/ai/v1.0/';

// 
export const  getTableData = (current, pageSize) => {
  return request(`${base}model/release/list`, {
    method: 'GET',
    params: {
      current,
      pageSize
    }
  }, {
    errorTitle: ''
  });
};

